.companyName {
  display: flex;
  gap: 30px;
  color: white;
  padding: 30px;
}

.companyNameClosed {
  display: none;
}

.sideNav {
  width: 100vw;
  height: 100vh;
  transition: width 0.3s ease-in-out;
  background-color: rgb(10, 25, 41);
  display: flex;
  flex-direction: column;
  // padding: 30px 0px;
}

.sideNavClosed {
  composes: sideNav;
  transition: width 0.3s ease-in-out;
  width: 5vw;
}

.sideItem {
  display: flex;
  align-items: center;
  // padding: 16px 32px;
  cursor: pointer;
  color: #b2bac2;
  text-decoration: none;
}

.sideItem:hover {
  background-color: #deeeff1c;
  color: white;
}

.sideItemActive {
  background-color: #deeeff1c;
  color: white;
  border-left: 3px solid white;
}

.linkText {
  padding-left: 16px;
  font-size: 18px;
}

.linkTextClosed {
  composes: linkText;
  display: none;
}

.menuBtn {
  align-self: center;
  align-self: flex-start;
  justify-self: flex-end;
  color: #b2bac2;
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding-left: 20px;
  margin-top: auto;
}

@media (min-width: 768px) {
  .sideNav {
    width: 15vw;
    transition: width 0.3s ease-in-out;
    background-color: rgb(10, 25, 41);
    display: flex;
    flex-direction: column;
    padding: 30px 0px;
  }

  .sideNavClosed {
    composes: sideNav;
    transition: width 0.3s ease-in-out;
    width: 5vw;
  }

  .sideItem {
    display: flex;
    align-items: center;
    padding: 16px 32px;
    cursor: pointer;
    color: #b2bac2;
    text-decoration: none;
  }

  .sideItem:hover {
    background-color: #deeeff1c;
    color: white;
  }

  .sideItemActive {
    background-color: #deeeff1c;
    color: white;
    border-left: 3px solid white;
  }

  .linkText {
    padding-left: 16px;
    font-size: 18px;
  }

  .linkTextClosed {
    composes: linkText;
    display: none;
  }

  .menuBtn {
    align-self: center;
    align-self: flex-start;
    justify-self: flex-end;
    color: #b2bac2;
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding-left: 20px;
    margin-top: auto;
  }
}
